@import './base.scss';
.loading{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    height: 100vh;
    width: 100%;
    z-index: 99;
    background: #0b134f;
    display: grid;
    place-items: center;
    font-family: "Orbitron", sans-serif;
    &__box{
        position: relative;
        width: 500px;
        height: 200px;
        border: 3px solid $green-color;
        border-top: 3px solid $dark-green-color;
        border-bottom: 3px solid $dark-green-color;
        @media(max-width: 600px){
            &{
                width: 90vw;
            }
        }
        
    }
    &__bar{
        width: 90%;
        height: 10px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%);
        background: #ccc;
        border-radius: 2px;
        &__inner{
            height: 100%;
            width: 0;
            border-radius: 2px;
            background: $green-color;
        }
    }
    &__text{
        position: relative;
        color: #fff;
        padding: 1rem;
        font-size: 20px;
        font-weight: 700;
        display: flex;
        align-items: center;
        justify-content: center;
        @media(max-width: 355px){
            &{
               font-size: 17px;
            }
        }
        &__dot{
            width: 15px;
            height: 15px;
            margin: 0 3px;
            border-radius: 50%;
            background: #fff;
            animation: pulse 1s infinite;
            @keyframes pulse {
                from {
                    opacity: 0;
                    background: $green-color;
                }
                to {
                    opacity: 1;
                }
            }
        }
        &__border{
            width: 85%;
            height: 1px;
            background: $dark-green-color;
            position: absolute;
            bottom: 0;
            left: 50%;
            transform: translateX(-50%);
        }
    }
    &__counter{
        position: absolute;
        top: 70%;
        left: 0;
        color: #fff;
        font-size: 20px;
        font-weight: 700;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 15px;
        &__number{
            color: $green-color;
        }
    }
    &__width_animation{
        animation: width_animation;
        @keyframes width_animation {
            0%{
                width: 85%;
            }
            100%{
                width: 280px;
            }
        }
    }
    &__svg{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        width: 65%;
        opacity: 0;
    }
}
