@import "./base.scss";

.reviews{
    position: relative;
    background: $darkish-blue;
    color: #fff;
    &__header{
        display: flex;
        align-items: center;
        gap: 1rem;
        .instructor{
            position: relative;
            background: #28316013;
            box-shadow:  0 8px 32px 0 $box-shadow-blue1;
            &__curve{
                position: absolute;
                top: -10px;
                left: 20px;
                width: 30%;
                height: 120%;
                border-left: 2px solid $red-color;
                border-radius: 50%;
                transform: rotate(-20deg);
                box-shadow: 0 8px 32px 0 $box-shadow-blue2;
            }
            &__infos{
                display: flex;
                align-items: center;
                gap: 15px;
                font-style: 17px;
                font-family: sans-serif;
            }
            &__img{
             width: 100px;
             height: 100px;
             border-radius: 40%;
             border-right: 2px solid $red-color;
             @media (min-width:400px){
                width: 150px;
                height: 150px;
             }   
            }
            &__col {
                display: flex;
                flex-direction: column;
                gap: 5px;
            }
            &__info{
                display: flex;
                align-items: center;
                gap: 10px;
                & img{
                    width: 20px;
                    height: 20px;
                }
            }
        }
    }
}

.swiper__wrapper{
    padding: 2rem 0;
}
.swiper{
    
    user-select: none;
}
.review{
    position: relative;
    display: flex;
    font-size: 16px;
    padding: 5px;
    &__card{
        position: relative;
        width: 100%;
        padding: 2rem 0;
        display: flex;
        @media (min-width:650px) {
            & {
                font-size: 15px;
            }
        }
    }
    & svg{
        width: 30px;
        fill:$red-color;
        @media (min-width:500px){
            width: 40px;
        }
    }
    &__topborder{
        width: 100%;
        height: 1px;
        position: absolute;
        top: 0;
        left: 0;
        background: #fff;
    }

    &__text{
        
        span{
            font-family: 'Haghisty_DEMO', sans-serif;
            font-size: 30px;
            font-weight: 600;
            color: $red-color;
        }
    }
    &__img{
        width: 70px;
        height: 100px;
        object-fit: cover;
        border-radius: 35px;
    }
    &__profile{
        position: absolute;
        left: 5rem;
        bottom: -2rem;
        display: flex;
        flex-direction: column;
        font-family: 'Josefin Sans Std', sans-serif;
        font-size: 17px;
    }
}

.swiper-slide{
    filter: blur(2px) !important;
    transform: scale(0.8) !important;
    transition: all 0.2s;
}
.swiper-slide-active{
    filter: blur(0) !important;
    transform: scale(1) !important;
}
.swiper__tools{
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: column-reverse;
    gap: 2rem;
    justify-content: space-between;
    margin-top: 6rem;
    padding: 0 4rem 0 2rem;
    @media (min-width:1227px) {
        & {
            flex-direction: row;
        }
    }
}
.swiper__buttons{
    display: flex;
    button{
        position: relative;
        background: transparent;
        padding: 1rem 3rem;
        border-radius: 50px;
        border: 1px solid #fff;
        margin-right: 2rem;
        user-select: none;
        &:after{
            display: none;
        }
        img{
            filter: invert(100%);
        }
        &:first-of-type{
            transform: rotate(180deg);
        }
        &:hover{
            background: #ffffff21;
        }
    }
    
}
.swiper-pagination {
    display: none;
    @media (min-width:979px) {
        display: block;
    }
    position: relative;
   .swiper-pagination-bullet {
    width: 35px;
    height: 4px;
    border-radius: 50px;
    background: #fff;
   }
   .swiper-pagination-bullet-active{
    width: 70px;
   }
}